<template>
  <view class="add-report-container">
    <view class="page-header">
      <text class="page-title">新增报表</text>
    </view>
    <scroll-view scroll-y class="form-scroll">
      <view class="form-card">
        <u-form
          :model="form"
          ref="uForm"
          labelPosition="left"
          labelWidth="40"
          labelAlign="left"
        >
          <u-form-item label="日期" prop="date">
            <view class="date-picker" @click="showCalendar">
              <text>{{ formatDate(form.date) }}</text>
              <view class="date-picker__right">
                <u-icon name="calendar" size="22" color="#2979ff"></u-icon>
              </view>
            </view>
          </u-form-item>
          <u-form-item label="店面名称" prop="storeName" required>
            <u-input v-model="form.storeName" placeholder="请输入店面名称" />
          </u-form-item>
          <u-form-item label="顾客名称" prop="customerName" required>
            <u-input v-model="form.customerName" placeholder="请输入顾客名称" />
          </u-form-item>

          <u-form-item label="业绩" prop="performance" required>
            <u-input
              v-model="form.performance"
              type="number"
              placeholder="请输入业绩金额"
            />
          </u-form-item>
          <u-form-item label="项目名称" prop="projectName">
            <u-input v-model="form.projectName" placeholder="请输入项目名称" />
          </u-form-item>
          <u-form-item label="分成" prop="commission">
            <u-input
              v-model="form.commission"
              type="number"
              placeholder="请输入分成金额"
            />
          </u-form-item>
          <u-form-item label="实际" prop="actual">
            <u-input
              v-model="form.actual"
              type="number"
              placeholder="请输入实际金额"
            />
          </u-form-item>
          <u-form-item label="操作" prop="operation">
            <u-input v-model="form.operation" placeholder="请输入操作信息" />
          </u-form-item>
          <u-form-item label="备注" prop="remark">
            <u-input
              v-model="form.remark"
              type="textarea"
              placeholder="请输入备注"
            />
          </u-form-item>
        </u-form>
      </view>
      <view class="bottom-placeholder"></view>
    </scroll-view>

    <view class="fixed-bottom">
      <u-button
        @click="submitReport"
        type="primary"
        class="submit-btn"
        :loading="submitting"
      >
        <u-icon name="checkmark" size="20" style="margin-right: 5px"></u-icon>
        提交报表
      </u-button>
    </view>
    <u-datetime-picker
      :show="showDatePicker"
      v-model="form.date"
      mode="date"
      @confirm="dateConfirm"
      :closeOnClickOverlay="true"
      title="选择日期"
      :maxDate="maxDate"
      :minDate="new Date('2025-01-01').getTime()"
      @close="closeDatePicker"
    ></u-datetime-picker>
    <!-- <u-calendar
      :show="showDatePicker"
      mode="single"
      @confirm="dateConfirm"
      @close="closeDatePicker"
      closeOnClickOverlay
      safeAreaInsetBottom
      minDate="2025-01-01"
      maxDate="2025-03-15"
      color="#2979ff"
    ></u-calendar> -->
  </view>
</template>

<script>
import FormValidator from "@/static/utils/formValidator.js";

export default {
  data() {
    return {
      submitting: false,
      showDatePicker: false,
      maxDate: new Date().getTime(), // 今天
      form: {
        date: new Date().getTime(),
        storeName: "",
        customerName: "",
        projectName: "",
        performance: "",
        commission: "",
        actual: "",
        operation: "",
        remark: "",
      },
      rules: {
        storeName: [{ required: true, message: "请输入店面名称" }],
        customerName: [{ required: true, message: "请输入顾客名称" }],
        performance: [
          { required: true, message: "请输入业绩金额" },
          { type: "number", message: "业绩必须为数字" },
        ],
        commission: [{ type: "number", message: "分成必须为数字" }],
        actual: [{ type: "number", message: "实际金额必须为数字" }],
      },
      userInfo: null,
    };
  },
  onLoad() {
    this.userInfo = uni.getStorageSync("userInfo");
    if (!this.userInfo) {
      uni.redirectTo({ url: "/pages/login/login" });
      return;
    }
    // this.checkTodayReport();
  },
  methods: {
    showCalendar() {
      this.showDatePicker = true;
    },
    closeDatePicker() {
      this.showDatePicker = false;
    },
    dateConfirm(e) {
      try {
        if (!e || !e.value) {
          uni.showToast({
            title: "请选择日期",
            icon: "none",
          });
          return;
        }

        const selectedDate = new Date(e.value).setHours(0, 0, 0, 0);
        const today = new Date().setHours(0, 0, 0, 0);

        if (selectedDate > today) {
          uni.showToast({
            title: "只能选择今天或之前的日期",
            icon: "none",
          });
          return;
        }

        // 更新日期
        this.$set(this.form, "date", e.value);
        this.showDatePicker = false;

        // 查询选择日期的报表数据
        //this.fetchReportByDate(e.value);
      } catch (error) {
        console.error("日期选择错误:", error);
        uni.showToast({
          title: "日期选择出错",
          icon: "none",
        });
      }
    },

    // 根据日期查询报表数据
    fetchReportByDate(timestamp) {
      uni.showLoading({
        title: "加载数据中...",
      });

      const date = new Date(timestamp);
      date.setHours(0, 0, 0, 0);

      uniCloud
        .callFunction({
          name: "getTodayReport", // 复用getTodayReport接口
          data: {
            userId: this.userInfo._id,
            date: date.getTime(),
          },
        })
        .then(res => {
          uni.hideLoading();
          if (res.result.code === 0) {
            if (res.result.data) {
              // 有数据，回显
              const formData = { ...this.form, ...res.result.data };
              this.form = formData;
              uni.showToast({
                title: "已加载该日期数据",
                icon: "none",
              });
            } else {
              // 无数据，清空表单（保留日期）
              const currentDate = this.form.date;
              this.form = {
                date: currentDate,
                storeName: "",
                customerName: "",
                projectName: "",
                performance: "",
                commission: "",
                actual: "",
                operation: "",
                remark: "",
              };
              uni.showToast({
                title: "该日期无数据",
                icon: "none",
              });
            }
          } else {
            uni.showToast({
              title: res.result.message || "查询数据失败",
              icon: "none",
            });
          }
        })
        .catch(err => {
          uni.hideLoading();
          console.error("查询数据失败:", err);
          uni.showToast({
            title: "查询数据失败",
            icon: "none",
          });
        });
    },
    formatDate(timestamp) {
      try {
        if (!timestamp) return "";

        if (typeof timestamp === "string" && isNaN(timestamp)) return timestamp;
        const date = new Date(Number(timestamp));
        if (isNaN(date.getTime())) return "";
        const year = date.getFullYear();
        const month = String(date.getMonth() + 1).padStart(2, "0");
        const day = String(date.getDate()).padStart(2, "0");
        return `${year}-${month}-${day}`;
      } catch (error) {
        console.error("日期格式化错误:", error);
        return "";
      }
    },
    checkTodayReport() {
      // 直接使用fetchReportByDate方法加载今天的数据
      const today = new Date();
      this.fetchReportByDate(today.getTime());
    },
    submitReport() {
      if (this.submitting) return; // 防止重复提交

      // 使用表单验证工具验证表单
      if (!FormValidator.validateAndShowError(this.form, this.rules)) {
        return;
      }

      // 验证通过，提交表单
      this.submitting = true;
      uni.showLoading({
        title: "提交中...",
      });

      // 判断是新增还是修改
      const isUpdate = !!this.form._id;
      const actionText = isUpdate ? "修改" : "新增";

      uniCloud
        .callFunction({
          name: "addReport",
          data: {
            userId: this.userInfo._id,
            ...this.form,
          },
        })
        .then(res => {
          this.submitting = false;
          uni.hideLoading();
          if (res.result.code === 0) {
            // 如果是新增，保存返回的ID
            if (!isUpdate && res.result.data && res.result.data._id) {
              this.$set(this.form, "_id", res.result.data._id);
            }

            uni.showToast({
              title: `${actionText}成功`,
              icon: "success",
            });

            // 更新完后不自动返回，允许用户继续编辑
            // 只有在新增时才返回
            if (!isUpdate) {
              setTimeout(() => {
                uni.navigateBack();
              }, 1500);
            }
          } else {
            uni.showToast({
              title: res.result.message || `${actionText}失败`,
              icon: "none",
            });
          }
        })
        .catch(err => {
          console.error(`${actionText}失败:`, err);
          this.submitting = false;
          uni.hideLoading();
          uni.showToast({
            title: `${actionText}失败，请重试`,
            icon: "none",
          });
        });
    },
  },
};
</script>

